<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item label="">
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	
		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="expand">
					<template scope="props">
						<el-table :data="props.row.sub_type" style="width: 100%">
							<el-table-column type="index" width="60">
							</el-table-column>
							<el-table-column prop="name" width="120" sortable>
							</el-table-column>
							<el-table-column prop="status" :formatter="formatSex" width="120" sortable>
							</el-table-column>
							<el-table-column prop="name" width="120" sortable>
							</el-table-column>
							<el-table-column inline-template :context="_self" min-width="320">
								<span>
									<el-button size="small" @click="handleEdit(row)"></el-button>
									<!--<el-button type="danger" size="small" @click="handleDel(row)"></el-button>-->
								</span>
							</el-table-column>
						</el-table>
					</template>
				</el-table-column>
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="name" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="status" label="" :formatter="formatSex" width="120" sortable>
				</el-table-column>
				<el-table-column prop="name" label="" width="120" sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" min-width="320">
					<span>
						<el-button size="small" @click="handleEdit(row)"></el-button>
						<el-button size="small" @click="handlesubAdd(row)"></el-button>
						<!--<el-button type="danger" size="small" @click="handleDel(row)"></el-button>-->
					</span>
				</el-table-column>
			</el-table>
		</template>
	
		<!---->
		<!--<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
							<el-pagination layout="total,sizes,prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 200, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
							</el-pagination>
						</el-col>-->
	
		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pic">
                    <el-upload action="" :file-list="filelist" :http-request="handleRequestOss" list-type="picture-card" :on-change="handlechange" :on-preview="handlePictureCardPreview" :on-remove="handleRemove" :on-success="getUpstr">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog v-model="dialogVisible" size="tiny">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>
				<el-form-item label="" prop="sort">
                    <el-input v-model="editForm.sort" auto-complete="off"></el-input>
					<el-input type="hidden" v-model="editForm.pic" auto-complete="off"></el-input>
                </el-form-item>
				<el-form-item label="" prop="note">
                    <el-input type="area" v-model="editForm.note" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"> </el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
            </div>
		</el-dialog>
	
		<el-dialog :title="editFormTtile" v-model="editFormVisibletest" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-form-item label="">
					<el-select v-model="editForm.sex" placeholder="">
						<el-option label="" :value="1"></el-option>
						<el-option label="" :value="0"></el-option>
						<el-option label="" :value="2"></el-option>
					</el-select>
				</el-form-item>
	
				<el-form-item label="">
					<el-select v-model="editForm.sex" placeholder="">
						<el-option label="" :value="1"></el-option>
						<el-option label="" :value="0"></el-option>
						<el-option label="" :value="2"></el-option>
					</el-select>
				</el-form-item>
	
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="11">
						<el-date-picker type="date" placeholder="" v-model="editForm.date1" style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<el-time-picker type="date" placeholder="" v-model="editForm.date2" style="width: 100%;"></el-time-picker>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.desc"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.desc"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request, { getUserListPage, removeUser, editUser, addUser } from 'api';
import config from 'config';
import client from 'common/sign'
export default {
	data() {
		return {
			filters: {
				name: ''
			},
			//
            dialogImageUrl: '',
            dialogVisible: false,
			filelist: [],
			users: [],
			total: 0,
			page: 1,
			pagesize: 10,
			listLoading: false,
			editFormVisible: false,//
			editFormVisibletest:false,
			editFormTtile: '',//
			shopdetailVisible: false,
			shopdetailtitle: '',
			shopdetailobj: {

			},
			//
			editForm: {
				id: 0,
				name: '',
				sex: -1,
				age: 0,
				birth: '',
				addr: ''
			},
			editLoading: false,
			btnEditText: ' ',
			editFormRules: {
				name: [
					{ required: true, message: '', trigger: 'blur' }
				]
			}

		}
	},
	methods: {
		//
		formatSex: function (row, column) {
			return row.status == 1 ? '' : row.status == 0 ? '' : '';
		},
		handleCurrentChange(val) {
			this.page = val;
			this.getUsers();
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
		},
		 //
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            console.log(file)
            console.log('qianzhi')
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
		 getUpstr() {
            console.log('chenggongshi ')
        },
        handlechange() {
            console.log('handlechange ')
        },
        handleBeforeup() {
            console.log('handleBeforeup ')
        },
		 handleRequestOss(files) {


            // client.list({
            //     'max-keys': 10
            // }).then(res => {
            //     console.log(res)
            // }).catch(err => {
            //     console.log(err)
            // })

            let file = files.file
            client.multipartUpload(file.name, file)
                .then(res => {
                    this.editForm.pic = res.url;
                }).catch(err => {
                    console.log(err)
                })
        },
		//
		getUsers() {

			let para = {
				page: this.page,
				name: this.filters.name,
				// shop_id:this.$route.params.id
			};
			this.listLoading = true;
			NProgress.start();
			// getUserListPage(para).then((res) => {
			// 	this.total = res.data.total;
			// 	this.users = res.data.users;
			// 	this.listLoading = false;
			// 	NProgress.done();
			// });
			request.get(config.api.goods.sortindex, para)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						// this.total = data.cnt.total;
						this.users = data.cnt;
						// this.pagesize = data.cnt.per_page || 10;
					}
				})
		},
		//
		handleDel: function (row) {
			//console.log(row);
			var _this = this;
			this.$confirm('?', '', {
				//type: 'warning'
			}).then(() => {
				_this.listLoading = true;
				NProgress.start();
				let para = { id: row.id };
				removeUser(para).then((res) => {
					_this.listLoading = false;
					NProgress.done();
					_this.$notify({
						title: '',
						message: '',
						type: 'success'
					});
					_this.getUsers();
				});

			}).catch(() => {

			});
		},
		showshopdetail(row) {
			this.shopdetailVisible = true;
			request.get(config.api.shop.shopqueuedetail, { shop_id: row.id })
				.then((res) => {
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.shopdetailobj = data.cnt;
					}
				})
		},
		//
		showorder(row) {
			this.$router.push('/shoporder');
		},
		//
		handleEdit: function (row) {
			this.editFormVisible = true;
			this.editFormTtile = '';
			this.editForm=row;
			this.filelist=[{name:'',url:row.app_pic}]
		},
		// or 
		editSubmit: function () {
			var _this = this;

			_this.$refs.editForm.validate((valid) => {
				if (valid) {

					_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						NProgress.start();
						_this.btnEditText = '';

						if (_this.editForm.id == 0) {
							//
							let para = _this.editForm;
							delete para.id;
							request.post(config.api.goods.addsort, para)
                                .then(res => {
                                    let { message, code, data } = res;
                                    _this.editLoading = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    if (code !== 200) {
                                        this.$notify({
                                            title: '',
                                            message: message,
                                            type: 'error'
                                        });
                                    } else {
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getUsers();
                                    }
                                })
						} else {
							//
                            let para = _this.editForm;
                            request.post(config.api.goods.updatesort, para)
                                .then(res => {
                                    let { message, code, data } = res;
                                    _this.editLoading = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    if (code !== 200) {
                                        this.$notify({
                                            title: '',
                                            message: message,
                                            type: 'error'
                                        });
                                    } else {
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getUsers();
                                    }
                                })

						}

					});

				}
			});

		},
		//
		handleAdd: function () {
			var _this = this;

			this.editFormVisible = true;
			this.editFormTtile = '';

			this.editForm.id = 0;
			this.editForm.name = '';
			this.editForm.pic = 1;
			this.editForm.pid = 0;
			this.filelist=[];
			this.editForm.sort = 0;
			this.editForm.note = '';

		},
		//
		handlesubAdd: function (row) {
			var _this = this;

			this.editFormVisible = true;
			this.editFormTtile = ''+row.name+'';

			this.editForm.id = 0;
			this.editForm.name = '';
			this.editForm.pic = 1;
			this.filelist=[];
			this.editForm.pid = row.id;
			this.editForm.sort = 0;
			this.editForm.note = '';

		}
	},
	mounted() {
		this.getUsers();
	}
}
</script>

<style scoped>
.el-card__header {
	background-color: #E4E4E4
}

.clearfix:before,
.clearfix:after {
	display: table;
	content: "";
}

.clearfix:after {
	clear: both
}
</style>


